import React, { useMemo } from "react";
import { observer } from "mobx-react-lite";
import { useTranslation } from "react-i18next";
import posthog from "posthog-js";
import { useRootStore } from "#shared/hooks";
import { BottleAnimationLoader } from "#shared/ui";
import { getStatus } from "#models/FetchStatus";
import { PrintWireTransferWrapper } from "./styled";
import translateInternationalInstructions from "../WireTransferInstructions/international";
import translateDomesticInstructions from "../WireTransferInstructions/domestic";
import vvLogo2x from "./vv-logo2x.png";
import { DepositEvent } from "../RootDepositPage";

const PrintWireTransfer = ({ match }) => {
    const { t } = useTranslation(["deposit"]);

    const store = useRootStore();
    const { data: referenceKeyFetch } = store.deposit.referenceKeyFetch;
    const isDone = getStatus(store.deposit.referenceKeyFetch).isDone();

    const {
        params: { type },
    } = match;

    const i18nInstructions = translateInternationalInstructions(t);
    const domesticInstructions = translateDomesticInstructions(t);
    const internationalInstructionsData = i18nInstructions.slice(0, -1);
    const lastInternationalInstructionsField = i18nInstructions[internationalInstructionsData.length - 1];

    const isInternational = type === "international-wire-transfer";

    const instructions = isInternational ? internationalInstructionsData : domesticInstructions;

    const pageIsDownloadedSuccessfully = useMemo(() => isDone, [isDone]);

    React.useEffect(() => {
        if (pageIsDownloadedSuccessfully) {
            setTimeout(() => {
                window.print();
                posthog.caputure(DepositEvent.DownloadWirePdf);
            }, 100);
        }
    }, [pageIsDownloadedSuccessfully]);

    return (
        <>
            {!pageIsDownloadedSuccessfully ? (
                <BottleAnimationLoader />
            ) : (
                <PrintWireTransferWrapper>
                    <div className="wrapper">
                        <div className="wrapper-inner">
                            <header>
                                <div className="main-header">
                                    <div className="head-nav flex-container">
                                        <a href="https://www.vinovest.co/">
                                            <img className="w-60" src={vvLogo2x} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </header>

                            <div className="page-content flex-container justify-content-between">
                                <div className="left-column-wrapper">
                                    <div className={`left-column ${isInternational ? "left-column-yellow" : ""}`}>
                                        <h2>
                                            {isInternational
                                                ? t("international_wire_transfer")
                                                : t("domestic_wire_transfer")}
                                        </h2>
                                        <div className="transfer-text">
                                            <p>{t("intl_description")}</p>
                                            <h4>{t("questions")}</h4>
                                            <a href="mailto:ir@vinovest.co">ir@vinovest.co</a>
                                            <a href="tel:+14152750965">+1 415-275-0965</a>
                                        </div>
                                    </div>
                                </div>
                                <div className="right-column-wrapper">
                                    <div className="right-column">
                                        <h3>{t("receiving_bank_dtls")}</h3>
                                        {instructions.map(({ title, value }) => (
                                            <React.Fragment key={title}>
                                                <h5>{title}</h5>
                                                <span role="textbox" contentEditable suppressContentEditableWarning>
                                                    {value}
                                                </span>
                                            </React.Fragment>
                                        ))}
                                    </div>
                                    <div className="right-column large-column">
                                        {isInternational && (
                                            <>
                                                <p className="txt-info">{lastInternationalInstructionsField.title}</p>
                                                <span>{lastInternationalInstructionsField.value}</span>
                                            </>
                                        )}
                                        <br />
                                        <span className="field-bg">{referenceKeyFetch}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </PrintWireTransferWrapper>
            )}
        </>
    );
};

export default observer(PrintWireTransfer);
